import React, {useState, useEffect} from 'react';

import Modal from 'react-modal';

import {ToastContainer, toast} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import classNames from 'classnames';
import {Splide, SplideSlide, SplideTrack} from '@splidejs/react-splide';

import style from './About.module.scss';

import {photos} from './PhotoItem/photoList';

import {PhotoItem} from './PhotoItem/PhotoItem';

export const About = () => {
  const [selectedPhoto, setSelectedPhoto] = useState('');

  const [isZoomed, setIsZoomed] = useState(false);

  const [isModalOpen, setIsModalOpen] = useState(false);

  const handleImageClick = () => {
    setIsZoomed(!isZoomed);
  };

  const [isMobile, setIsMobile] = useState(window.innerWidth < 600);

  const handleIndexPhoto = (index) => {
    // setIndexPhoto(index);
    setSelectedPhoto(photos[index].img);
  };


  const openModal = () => {
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedPhoto(null);
    setIsZoomed(false);
  };

  const notify = () => {
    toast.info(
      <div>
            !{' '}
        <a
          href="https://t.me/bulldogpubmurmansk"
          target="_blank" rel="noopener noreferrer"
          style={{
            color: 'var(--gold)',
            textDecoration: 'underline',
          }}>
             Telegram
        </a>,   !
      </div>,
      {
        position: 'top-right',
        autoClose: 7000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
        style: {
          backgroundColor: 'var(--black)', //  
          color: 'white', //  
          padding: '16px', //  
          borderRadius: '8px', //  
          fontSize: '16px', //  
        },
      }
    );
  };

  useEffect(() => {
    notify();

    const handleResize = () => {
      setIsMobile(window.innerWidth < 600);
    };

    // useEffect(() => {

    // }, []);

    window.addEventListener('resize', handleResize);

    return () => {
      window.removeEventListener('resize', handleResize);
      document.body.style.overflow = 'auto';
    };
  }, [isModalOpen]);

  return <article
    className={style.about}>
    <div
      className='container'>

      <h2 className='title_size_2'> </h2>
      <div className={style.about__wrapper}>
        <div
          className={style.about__description}>
          <div className={style.about__paragraf_wrapper}>
            <p className={style.about__text}>
              - &quot;&quot;   -           .               .
            </p>
            <p className={style.about__text}>
                  .      8   FullHD   -   .
            </p>

            <p className={style.about__text}>
                   .  ,   ,        .           .
            </p>
          </div>
        </div>

        {isMobile ? (<Splide
          hasTrack={false}
          options={{
            type: 'loop',
            role: 'tab',
            height: 350,
            arrows: true,
          }}>
          <SplideTrack>
            {photos.map((item, index) =>
              <SplideSlide
                key={index}
                pos={index}
              >
                <PhotoItem
                  pos={index}
                  data={item}
                  handleIndexPhoto={handleIndexPhoto} />
              </SplideSlide>)}
          </SplideTrack>
        </Splide>) :
          (<div className={classNames(style.about__photos, style.about__columns)}>
            {photos.slice(0, 6).map((item, index) =>
              <PhotoItem
                key={index}
                pos={index}
                data={item}
                handleIndexPhoto={handleIndexPhoto} />
            )}
          </div>)
        }
      </div>

      <ToastContainer />

    </div>

    {selectedPhoto && (

      <Modal
        isOpen={openModal}
        onRequestClose={closeModal}
        contentLabel="Full Screen Photo"
        style={{
          overlay: {
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            zIndex: 3,
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          },
          content: {
            inset: null,
            margin: 'auto',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            gap: '100px',
            maxHeight: '100%'
          }
        }}
      >
        <img
          src={selectedPhoto}
          alt="Full Screen Photo"
          onClick={handleImageClick}
        />
        <button
          onClick={closeModal}
          className={style.about__btnClose}
        ></button>
      </Modal>
    )}

  </article >;
};
